import axios from "axios";
import router from "@/router";

let baseURL = "";
if (process.env.NODE_ENV === "development") {
  baseURL = "/api";
} else if (process.env.VUE_APP_TYPE === "gitee") {
  baseURL = window.baseURL;
} else if (process.env.VUE_APP_TYPE === "service") {
  baseURL = "";
}
const service = axios.create({
  baseURL,
  timeout: 1000 * 100, // 100秒超时
  withCredentials: true, // 请求默认携带认证
});

function logoutHandle() {
  router.replace("/login");
}

service.interceptors.request.use((config) => {
  config.headers = {
    authorization: sessionStorage.getItem("authorization"),
  };
  config.params = config.params || {};
  config.params.random = Math.random();
  return config;
}, (err) => Promise.reject(err));

service.interceptors.response.use((response) => {
  const { data, status } = response || {};
  if (status === 401) {
    return logoutHandle();
  } else {
    if (data instanceof Blob) {
      return data;
    }
    const { returnCode, returnMsg } = data || {};
    if (returnCode === 100) {
      logoutHandle();
    } else if (returnCode === 0) {
      return data;
    } else {
      return Promise.reject(returnMsg);
    }
  }
}, (err) => {
  const { status } = err.response || {};
  if (status === 0) {
    return window.$Message.error("外网服务未开启");
  }
  if (status === 429) {
    return window.$Message.error("服务繁忙，请稍后重试");
  }
  if (status === 401) {
    return logoutHandle();
  }
  return Promise.reject(err);
});

const postBlob = (url, data, fileName) => new Promise((resolve, reject) => {
  service({
    method: "post",
    url,
    responseType: "blob",
    data,
  }).then((res) => {
    const blob = new Blob([res], { type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=utf-8" });
    const url = window.URL.createObjectURL(blob);
    const link = document.createElement("a");
    link.href = url;
    link.download = fileName;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    URL.revokeObjectURL(blob);
    resolve();
  }).catch((err) => {
    reject(err);
  });
});

