import { login } from "@/axios";
import EventBus from "./bus";
import { Spin } from "view-ui-plus";
import CryptoJS from "crypto-js";

const key = CryptoJS.enc.Utf8.parse("47FC4124CF94E070"); // 密钥
const iv = CryptoJS.enc.Utf8.parse("ovOh2xYoRdfATob6"); // 密钥偏移量

// 加密方法
export function aesEncode(word) {
  const srcs = CryptoJS.enc.Utf8.parse(word);
  // iv(初始变量)
  // key(加密密钥)
  // mode(加密模式 主要有CBC(默认)、CFB、CTR、OFB、ECB)
  // padding(填充方式 主要有Pkcs7(默认)、Iso97971、AnsiX923、Iso10126、ZeroPadding)
  const encrypted = CryptoJS.AES.encrypt(srcs, key, { iv, mode: CryptoJS.mode.CBC, padding: CryptoJS.pad.Pkcs7 });
  const encryptedBase64Data = CryptoJS.enc.Base64.stringify(encrypted.ciphertext);
  return encodeURIComponent(encryptedBase64Data);
}

// 解密方法
export function aesDecode(word) {
  word = decodeURIComponent(word);
  const encryptedHexStr = CryptoJS.enc.Base64.parse(word);
  const srcs = CryptoJS.enc.Base64.stringify(encryptedHexStr);
  const decrypt = CryptoJS.AES.decrypt(srcs, key, { iv, mode: CryptoJS.mode.CBC, padding: CryptoJS.pad.Pkcs7 });
  const decryptedStr = decrypt.toString(CryptoJS.enc.Utf8);
  return decryptedStr.toString();
}

function createWebSocket(data) {
  let http = "";
  if (process.env.NODE_ENV === "development") {
    http = "http://localhost:3000";
  } else if (process.env.VUE_APP_TYPE === "gitee") {
    http = window.baseURL;
  } else if (process.env.VUE_APP_TYPE === "service") {
    http = data.internet;
  }
  const wsUrl =  `${http.replace(/^http/, "ws")}/socket/${new Date().format("YYYYMMDDHHmmss")}`;
  window.socket = new WebSocket(wsUrl);
  window.socket.addEventListener("open", () => {
    console.log("连接ws", wsUrl);
  });
  window.socket.addEventListener("message", (e) => {
    try {
      const data = JSON.parse(e.data);
      const { type, ...params } = data;
      EventBus.$emit(type, params);
    } catch (err) {
      EventBus.$emit(e.data);
    }
  });
  window.socket.addEventListener("close", () => {
    console.log("关闭ws");
  });
}

function setInforByLogin(userInfo, data) {
  const token = aesEncode(`${userInfo.userName}/${userInfo.password}`);
  sessionStorage.setItem("token", token);
  if (localStorage.getItem("remember")) {
    localStorage.setItem("token", token);
  }
  sessionStorage.setItem("authorization", data.Authorization);
  sessionStorage.setItem("webConfig", JSON.stringify(data.webConfig));
  document.title = data.webConfig.appName;
  window.APPNAME = data.webConfig.appName;
  sessionStorage.setItem("website", JSON.stringify(data.website));
  createWebSocket(data.website);
}

export async function loginByPwd(userInfo) {
  try {
    Spin.show();
    let isEncode = false;
    const token = sessionStorage.getItem("token") || localStorage.getItem("token");
    if (token) {
      const [, password] = aesDecode(token)?.split("/");
      isEncode = password === userInfo.password;
    }
    if (!isEncode) {
      userInfo.password = CryptoJS.MD5(userInfo.password).toString();
    }
    const { returnCode, data } = await login({
      userName: userInfo.userName,
      password: userInfo.password,
    }) || {};
    if (returnCode === 0) {
      setInforByLogin(userInfo, data);
    }
  } finally {
    Spin.hide();
  }
}

export async function loginByToken(token) {
  try {
    Spin.show();
    const [userName, password] = aesDecode(token)?.split("/");
    const { returnCode, data } = await login({
      userName,
      password,
    }) || {};
    if (returnCode === 0) {
      setInforByLogin({ userName, password }, data);
    }
  } finally {
    Spin.hide();
  }
}
