export const isMobile = /(ipod|ipad|iphone|android|coolpad|mmp|smartphone|midp|wap|xoom|symbian|j2me|blackberry|wince)/i.test(navigator.userAgent);

export function getMapNavigation({ longitude, latitude, name }) {
  if (!longitude || !latitude) return;
  if (isMobile) {
    return `https://uri.amap.com/navigation?to=${longitude},${latitude},${name || "终点"}&policy=1&src=mypage`;
  } else {
    const webConfig = JSON.parse(sessionStorage.getItem("webConfig") || "{}");
    const { center } = webConfig.map || {};
    return `https://uri.amap.com/navigation?from=${center[0]},${center[1]},起点&to=${longitude},${latitude},${name || "终点"}&policy=1&src=mypage`;
  }
}
