import { createRouter, createWebHashHistory } from "vue-router";
import { loginByToken } from "@/utils";
import { Message } from "view-ui-plus";

const routes = [
  {
    path: "/",
    redirect: { name: "login" }
  },
  {
    path: "/login",
    name: "login",
    component: () => import("@/views/login")
  },
  {
    path: "/home",
    name: "home",
    component: () => import("@/views/home"),
    redirect: { name: "cashierMng" },
    children: [
      {
        path: "/cashierMng",
        name: "cashierMng",
        component: () => import("@/views/cashierMng")
      },
      {
        path: "/goodMng",
        name: "goodMng",
        component: () => import("@/views/goodMng")
      },
      {
        path: "/userMng",
        name: "userMng",
        component: () => import("@/views/userMng")
      },
      {
        path: "/orderMng",
        name: "orderMng",
        component: () => import("@/views/orderMng")
      },
      {
        path: "/specialMng",
        name: "specialMng",
        component: () => import("@/views/specialMng")
      },
      {
        path: "/statistiAnalysis",
        name: "statistiAnalysis",
        component: () => import("@/views/statistiAnalysis")
      },
    ]
  },
  {
    path: "/mobileHome",
    name: "mobileHome",
    component: () => import("@/viewsMobile/mobileHome"),
    redirect: { name: "mobileGood" },
    children: [
      {
        path: "/mobileGood",
        name: "mobileGood",
        component: () => import("@/viewsMobile/mobileGood")
      },
      {
        path: "/mobileCashier",
        name: "mobileCashier",
        component: () => import("@/viewsMobile/mobileCashier")
      },
      {
        path: "/mobileOrder",
        name: "mobileOrder",
        component: () => import("@/viewsMobile/mobileOrder")
      },
      {
        path: "/mobileUser",
        name: "mobileUser",
        component: () => import("@/viewsMobile/mobileUser")
      },
      {
        path: "/mobileSpecial",
        name: "mobileSpecial",
        component: () => import("@/viewsMobile/mobileSpecial")
      },
      {
        path: "/mobileEnter",
        name: "mobileEnter",
        meta: {
          title: "扫码录入",
        },
        component: () => import("@/viewsMobile/mobileEnter")
      },
    ]
  },
  {
    path: "/:pathMatch(.*)",
    redirect: { name: "home" }
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes
});

router.beforeEach(async (to, from, next) => {
  if (to.query.token) {
    try {
      await loginByToken(to.query.token);
    } catch (err) {
      Message.error(`自动登录失败：${err.message || err}`);
      next("/login");
    }
  }
  if (to.path === "/login") {
    next();
  } else if (!sessionStorage.getItem("authorization")) {
    if (from.path === "/") {
      next("/login");
    } else {
      next({
        name: "login",
        query: { realName: to.name, query: JSON.stringify(to.query), params: JSON.stringify(to.params) }
      });
    }
  } else if (window.isMobile && !to.name.includes("mobile")) {
    next({
      name: `mobile${to.name[0].toUpperCase()}${to.name.slice(1).replace("Mng", "")}`,
      query: to.query,
      params: to.params,
    });
  } else if (!window.isMobile && to.name.includes("mobile")) {
    next({
      name: `${to.name.replace("mobile", "").toLowerCase()}Mng`,
      query: to.query,
      params: to.params,
    });
  } else {
    next();
  }
});

export default router;
