Date.prototype.format = function (format) {
  const o = {
    "M+": this.getMonth() + 1,
    "D+": this.getDate(),
    "H+": this.getHours(),
    "m+": this.getMinutes(),
    "s+": this.getSeconds(),
  };

  if (/(Y+)/.test(format)) {
    format = format.replace(RegExp.$1, `${this.getFullYear()}`.substr(4 - RegExp.$1.length));
  }

  for (let k in o) {
    if (new RegExp(`(${k})`).test(format)) {
      format = format.replace(RegExp.$1, RegExp.$1.length == 1 ? o[k] : `00${o[k]}`.substr(`${o[k]}`.length));
    }
  }
  return format;
};

Date.prototype.subtract = function (count = 1, unit = "days") {
  if (unit === "days") {
    return new Date(this.setDate(this.getDate() - count));
  } else if (unit === "months") {
    return new Date(this.setMonth(this.getMonth() - count));
  }
};

Date.prototype.add = function (count = 1, unit = "days") {
  if (unit === "days") {
    return new Date(this.setDate(this.getDate() + count));
  } else if (unit === "months") {
    return new Date(this.setMonth(this.getMonth() + count));
  }
};

Date.prototype.startOf = function (unit = "day") {
  if (unit == "day") {
    return new Date(this.getFullYear(), this.getMonth(), this.getDate(), 0, 0, 0, 0);
  } else if (unit == "week") {
    const day = this.getDay();
    const offset = day == 0 ? 6 : day - 1;
    return new Date(this.getFullYear(), this.getMonth(), this.getDate() - offset, 0, 0, 0, 0);
  } else if (unit == "month") {
    return new Date(this.getFullYear(), this.getMonth(), 1, 0, 0, 0, 0);
  } else if (unit == "year") {
    return new Date(this.getFullYear(), 0, 1, 0, 0, 0, 0);
  }
};

Date.prototype.endOf = function (unit = "day") {
  if (unit == "day") {
    return new Date(this.getFullYear(), this.getMonth(), this.getDate(), 23, 59, 59, 999);
  } else if (unit == "week") {
    const day = this.getDay();
    const offset = day == 0 ? 0 : 7 - day;
    return new Date(this.getFullYear(), this.getMonth(), this.getDate() + offset, 23, 59, 59, 999);
  } else if (unit == "month") {
    const map = [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31];
    const isLeapYear = (year) => (year % 4 === 0 && year % 100 !== 0) || year % 400 === 0;
    const getDaysInMonth = (year, month) => {
      if (month === 1 && isLeapYear(year)) {
        return 29;
      }
      return map[month];
    };
    const days = getDaysInMonth(this.getFullYear(), this.getMonth());
    return new Date(this.getFullYear(), this.getMonth(), days, 23, 59, 59, 999);
  } else if (unit == "year") {
    return new Date(this.getFullYear(), 11, 31, 23, 59, 59, 999);
  }
};
