import { createApp } from "vue";
// 外部引入，减少打包体积
// const { createApp } = Vue;
import App from "./App.vue";
import router from "./router";

// 全局样式
import "@/assets/styles/index.less";

const app = createApp(App);

// 使用iview
import "view-ui-plus/dist/styles/viewuiplus.css";
import {
  Button,
  Icon,
  Message,
  Form,
  FormItem,
  Modal,
  Spin,
  Badge,
  RadioGroup,
  Radio,
  Select,
  Option,
} from "view-ui-plus";
app.component("Button", Button);
app.component("Icon", Icon);
app.component("Form", Form);
app.component("FormItem", FormItem);
app.component("Modal", Modal);
app.component("Spin", Spin);
app.component("Badge", Badge);
app.component("RadioGroup", RadioGroup);
app.component("Radio", Radio);
app.component("Select", Select);
app.component("Option", Option);

// 使用vant
import "vant/lib/index.css";
import {
  Toast,
  showToast,
  showSuccessToast,
  showFailToast,
  Search,
  Cell,
  ActionSheet,
  RadioGroup as vanRadioGroup,
  Radio as vanRadio,
} from "vant";
app
  .use(Toast)
  .use(Search)
  .use(Cell)
  .use(ActionSheet)
  .use(vanRadioGroup)
  .use(vanRadio);

// 使用全局组件
import {
  viInput,
  viInputNumber,
  viLayoutUpDown,
  viSearchForm,
  viTablePage,
  viEmpty,
} from "@/components";
app.component("viInput", viInput);
app.component("viInputNumber", viInputNumber);
app.component("viLayoutUpDown", viLayoutUpDown);
app.component("viSearchForm", viSearchForm);
app.component("viTablePage", viTablePage);
app.component("viEmpty", viEmpty);

import { EventBus, isMobile } from "@/utils";

// 全局变量
Message.config({
  duration: 3, // 单位s
});
const config = {
  duration: 3000, // 单位ms
  className: "van-toast van-toast-120"
};
window.$Message = isMobile ? {
  success: (message) => showSuccessToast({ message: message.replace(/<br>/g, "\n"), ...config }),
  error: (message) => showFailToast({ message: message.replace(/<br>/g, "\n"), ...config }),
  info: (message) => showToast({ message: message.replace(/<br>/g, "\n"), ...config }),
} : Message;
app.config.globalProperties.$Message = window.$Message;
app.config.globalProperties.$Modal = Modal;
app.config.globalProperties.$EventBus = EventBus;
window.isMobile = isMobile;

console.alert = (function (fun) {
  return isMobile ? function () {
    const mes = [];
    Array.from(arguments).forEach((item, index) => {
      mes[index] = item.message || JSON.stringify(item);
    });
    alert(mes.join("\n"));
  } : fun;
})(console.log);

// 防止刷新丢失
const webConfig = JSON.parse(sessionStorage.getItem("webConfig") || "{}");
document.title = webConfig.appName || "登录";
window.APPNAME = webConfig.appName || "";

// 挂载
app.use(router).mount("#app");
